  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.slide-container {
    width: 100%;
    margin: auto; }
  
  h3 {
    text-align: center; }
  
  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 400px;
  }
  
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }
  
  .each-fade {
    display: flex;
  }
  
  .each-fade .image-container {
    width: 100%;
    overflow: hidden;
  }
  
  .each-fade .image-container img {
    width: 100%;
  }
  
  .each-fade h2 {
    width: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #adceed;
  }

  .react-slideshow-container .nav{
      height : 400px;
     
      border-radius: 0em;
      background-color: rgba(0,0,0,0);
      	
  }

  .react-slideshow-container .nav:first-of-type:hover{
    background: 
    linear-gradient( to right, rgba(0,78,96,0.8), rgba(0,78,96,0.0) )
  }

  .react-slideshow-container .nav:last-of-type:hover{
    background: 
    linear-gradient( to right, rgba(0,78,96,0.0), rgba(0,78,96,0.8) )
  }

  .react-slideshow-container + div.indicators{
    bottom: 50px;
    position: relative;
  }

  .react-slideshow-container + div.indicators > div.active, .react-slideshow-container + div.indicators > div:hover {
    width: 14px;
    height: 14px;
    background: rgba(0,78,96,1.0);
  }

  .react-slideshow-container + div.indicators > div {
        width: 14px;
        height: 14px;
        background: rgba(0,0,0,0);
        border : 1px solid rgba(0,78,96,1.0);
        border-width: 2px;
    }
@charset "utf-8";
#header{position:relative; z-index:9;}
.header_top{padding: 0 100px;height:60px; background:rgba(0,78,96,1.0); line-height:60px; color:#fff;}
.header_top p{float:left; margin-left:20px; font-size:16px; font-weight:500}
.header_top p a{color:#fff; padding:0 10px;}
.top_menu{float:right; margin-right:20px;}
.top_menu li{float:left;}
.top_menu li a{color:#fff; padding:0 10px;border-right:1px solid #fff;}
.top_menu li:last-child a{border-right:none;}
.top_menu li:hover, .top_menu li.active{
    font-weight:600; background-color:rgb(0,78,96); text-shadow:2px 2px 3px rgba(0,0,0,0.2);
}



